(item.id)\"\n\t\t\t\t:avatar-url=\"getAvatarUrl()\"\n\t\t\t\t:avatar-username=\"getAvatarUsername()\"\n\t\t\t\t:avatar-is-no-user=\"true\"\n\t\t\t\t:main-text=\"'Task #' + parseTargetMtype(item) + ' - ' + item.files_total + ' file(s) (' + formatBytes(item.files_total_size) + ')'\"\n\t\t\t\t:sub-text=\"`${item.owner} - ${getStatusBadge(item)}`\" />\n\t\t</template>\n\t\t<template #empty-content>\n\t\t\t<div class=\"empty-tasks-list\">\n\t\t\t\t<img src=\"data:image/svg+xml;base64,\" width=\"96\" height=\"96\">\n\t\t\t\t<strong>{{ t('mediadc', 'No tasks yet.') }}</strong>\n\t\t\t\t<a :href=\"getAppUrl()\">{{ t('mediadc', 'Create a new one!') }}</a>\n\t\t\t</div>\n\t\t</template>\n\t</DashboardWidget>\n</template>\n\n<script>\nimport { generateUrl } from '@nextcloud/router'\nimport { DashboardWidget, DashboardWidgetItem } from '@nextcloud/vue-dashboard'\nimport { loadState } from '@nextcloud/initial-state'\nimport Formats from '../mixins/Formats'\nimport { getCurrentUser } from '@nextcloud/auth'\n\nconst tasks = loadState('mediadc', 'mediadc-recent-tasks')\n\nexport default {\n\tname: 'Dashboard',\n\tcomponents: {\n\t\tDashboardWidget,\n\t\tDashboardWidgetItem,\n\t},\n\tmixins: [\n\t\tFormats,\n\t],\n\tdata() {\n\t\treturn {\n\t\t\ttasks,\n\t\t\tloading: true,\n\t\t}\n\t},\n\tasync mounted() {\n\t\tthis.loading = false\n\t},\n\tmethods: {\n\t\tgetAppUrl() {\n\t\t\treturn generateUrl('/apps/mediadc')\n\t\t},\n\t\tgetItemUrl(taskId) {\n\t\t\treturn generateUrl(`/apps/mediadc/tasks/${taskId}`)\n\t\t},\n\t\tgetAvatarUrl() {\n\t\t\treturn generateUrl(`/avatar/${getCurrentUser().uid}/32`)\n\t\t},\n\t\tgetAvatarUsername() {\n\t\t\treturn getCurrentUser().uid\n\t\t},\n\t},\n}\n</script>\n\n<style scoped>\nh1 {\n\tpadding: 20px;\n\ttext-align: center;\n}\n\nul {\n\tdisplay: block;\n\theight: 100%;\n\toverflow-y: scroll;\n}\n\n.empty-tasks-list {\n\tpadding: 20px;\n\twidth: 100%;\n\theight: 80%;\n\tdisplay: flex;\n\talign-items: center;\n\tjustify-content: center;\n\tbox-sizing: border-box;\n\tflex-direction: column;\n}\n\n.empty-tasks-list strong {\n\tmargin: 10px 0 0;\n}\n\n.empty-tasks-list a {\n\ttext-decoration: underline;\n}\n</style>\n"],"sourceRoot":""}]);
